import React, { useState, useRef, useEffect } from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import FirstPage from '@mui/icons-material/FirstPage';
import LastPage from '@mui/icons-material/LastPage';

const WallPaper = styled('div')({
    position: 'absolute',
    width: '100%',
    height: '100%',
    top: 0,
    left: 0,
    overflow: 'hidden',
    background: 'linear-gradient(rgb(255, 38, 142) 0%, rgb(255, 105, 79) 100%)',
    transition: 'all 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s',
    '&::before': {
      content: '""',
      width: '140%',
      height: '140%',
      position: 'absolute',
      top: '-40%',
      right: '-50%',
      background:
        'radial-gradient(at center center, rgb(62, 79, 249) 0%, rgba(62, 79, 249, 0) 64%)',
    },
    '&::after': {
      content: '""',
      width: '140%',
      height: '140%',
      position: 'absolute',
      bottom: '-50%',
      left: '-30%',
      background:
        'radial-gradient(at center center, rgb(247, 237, 225) 0%, rgba(247, 237, 225, 0) 70%)',
      transform: 'rotate(30deg)',
    },
  });

const Widget = styled('div')(({ theme }) => ({
    padding: 16,
    borderRadius: 16,
    width: 343,
    maxWidth: '100%',
    margin: 'auto',
    position: 'relative',
    zIndex: 1,
    backgroundColor: 'rgba(255,255,255,0.4)',
    backdropFilter: 'blur(40px)',
    ...theme.applyStyles('dark', {
      backgroundColor: 'rgba(0,0,0,0.6)',
    }),
  }));

  interface Song {
    id: number;
    category: number;
    image: string;
    video: string;
    multi_files: boolean;
    title: string;
    youtube: string;
  }
  
  interface Props {
    songs: Song[];
    onSelectSong: (id: number) => void;
    currentSongIndex: number;
    currentPage: number;
    itemsPerPage: number;
    onPageChange: (id: number) => void;
    onTotalDuration: number;
  }

const Playlist: React.FC<Props> = ({ songs, onSelectSong , currentSongIndex, currentPage, itemsPerPage, onPageChange, onTotalDuration }) => {
  const totalPages = Math.ceil(songs.length / itemsPerPage);
  console.log(currentPage);
  const startIndex = currentPage * itemsPerPage;
  console.log(startIndex);
  console.log(startIndex + itemsPerPage);
  const [selectedSongs, setSelectedSongs] = useState<Song[]>([]);
  console.log(selectedSongs);
  const [totalHours,setTotalHours] = useState(0);
  const [totalMinutes,setTotalMinutes] = useState(0);
  const [totalSeconds,setTotalSeconds] = useState(0);

  useEffect(() => {
    setSelectedSongs(songs.slice(startIndex, startIndex + itemsPerPage));
  }, [songs,startIndex,itemsPerPage]);

  const [category,setCategory] = useState([
    {
      id:1,
      title: 'Muzica Populara'
    },
    {
      id:2,
      title: 'Manele'
    },
    {
      id:3,
      title: 'Usoara'
    }
  ]);
  
    const handleCategoryChange = (e) =>{
      const category_id = parseInt(e.target.value);
      console.log(selectedSongs);
      console.log(category_id);
      console.log(songs);
      if(category_id==0){
        const filteredSongs = songs.slice(startIndex, startIndex + itemsPerPage);
        console.log(filteredSongs);
        setSelectedSongs(filteredSongs);
        console.log(selectedSongs);
      }else{
        const filteredSongs = songs.filter(song => song.category===category_id);
        console.log(filteredSongs);
        setSelectedSongs(filteredSongs);
        console.log(selectedSongs);
      }
    }

    const capitalizeFirstLetter = (string) => {
      //string = string.toLowerCase();
      //return string.replace(/^./, string[0].toUpperCase());
      return string
        .toLowerCase()
        .split(' ')
        .map(word => word.charAt(0).toUpperCase() + word.slice(1))
        .join(' ');
    };

    useEffect(()=>{
      setTotalHours(Math.floor(onTotalDuration / 3600));
      setTotalMinutes(Math.floor((onTotalDuration % 3600) / 60));
      setTotalSeconds(Math.floor(onTotalDuration % 60));
    },[onTotalDuration]);

    return (
        <Box sx={{ width: '90%', overflow: 'hidden', position: 'relative', p: 3 , margin: '0 auto' }}>
          <Widget>
            <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <div style={{width:'100%', height:'1.2rem', display:'flex', flexDirection:'row', alignItems:'baseline', marginBottom:'2rem'}}>  
            <div style={{display:'flex', flexDirection:'row', alignItems:'baseline', marginBottom:'2rem'}}>
              <h2 style={{marginLeft:'8rem'}}>Playlist</h2>
            </div>
            <select style={{marginLeft:'auto'}} onChange={(e)=>handleCategoryChange(e)}>
              <option value='0'>Categorii</option>
              <option value='1'>{category[0].title}</option>
              <option value='2'>{category[1].title}</option>
              <option value='3'>{category[2].title}</option>
            </select>
            </div>
            <h4 style={{marginTop:'0.5rem',marginBottom:'-1rem'}}>Total Duration: {totalHours}:{totalMinutes}:{totalSeconds}</h4>
            <ul className="text-center" style={{listStyle:'none',width:'100%',height:'23rem',overflowY:'auto'}}>
                {selectedSongs.map((song, index) => (
                <li 
                    key={index} 
                    onClick={() => onSelectSong(song.id)} 
                    style={{
                        display:'flex',
                        flexDirection:'row',
                        cursor:'pointer',
                        backgroundColor: song.id === currentSongIndex+1 ? '#222' : 'transparent',
                        padding: '10px',
                        borderRadius: '5px',
                        marginTop: '5px',
                        marginRight: '1rem',
                        marginLeft: '-1.5rem'
                      }}
                >
                    <img src={song.image} alt={song.title} style={{ width: '30%', height: '50%', marginRight: '10px' }} />
                    <div style={{
                        display:'flex',
                        flexDirection:'column',
                      }}>
                      <div style={{width:'100%',overflow:'hidden',color:song.id === currentSongIndex+1 ? 'aquamarine' : '#222',backgroundColor:song.id === currentSongIndex+1 ? '#222' : 'transparent'}}><table><tbody><tr><td><strong>{song.id}</strong></td><td><strong>{category[song.category-1].title}</strong></td></tr></tbody></table></div>
                      <div style={{width:'100%',overflow:'hidden',color:song.id === currentSongIndex+1 ? 'aquamarine' : '#222',backgroundColor:song.id === currentSongIndex+1 ? '#222' : 'transparent'}}>{capitalizeFirstLetter(song.title)}</div>
                    </div>
                </li>
                ))}
            </ul>
            <div>
            <IconButton aria-label="previous song" disabled={currentPage === 0}>
              <FirstPage fontSize="large" onClick={()=>onPageChange(0)} />
            </IconButton>
            <Button onClick={() => onPageChange(currentPage - 1)} disabled={currentPage === 0}>
              <strong><span style={{fontSize:'large',color:currentPage===0?'rgba(0, 0, 0, 0.26)':'black'}}>&lt;&lt;</span></strong>
            </Button>
            <span style={{color:'black'}}> Page {currentPage + 1} of {totalPages} </span>
            <Button onClick={() => onPageChange(currentPage + 1)} disabled={currentPage === totalPages - 1}>
            <strong><span style={{fontSize:'large',color:currentPage===totalPages - 1?'rgba(0, 0, 0, 0.26)':'black'}}>&gt;&gt;</span></strong>
            </Button>
            <IconButton aria-label="next song" disabled={currentPage === totalPages - 1}>
              <LastPage fontSize="large" onClick={()=>onPageChange(totalPages)} />
            </IconButton>
      </div>
            </Box>
          </Widget>
          <WallPaper />
        </Box>
    );
}

export default Playlist;