import React, { useState, useRef, useEffect } from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Slider from '@mui/material/Slider';
import IconButton from '@mui/material/IconButton';
import Stack from '@mui/material/Stack';
import PauseRounded from '@mui/icons-material/PauseRounded';
import PlayArrowRounded from '@mui/icons-material/PlayArrowRounded';
import FastForwardRounded from '@mui/icons-material/FastForwardRounded';
import FastRewindRounded from '@mui/icons-material/FastRewindRounded';
import FirstPage from '@mui/icons-material/FirstPage';
import LastPage from '@mui/icons-material/LastPage';
import VolumeUpRounded from '@mui/icons-material/VolumeUpRounded';
import VolumeDownRounded from '@mui/icons-material/VolumeDownRounded';

import FormLabel from '@mui/material/FormLabel';
import FormControl from '@mui/material/FormControl';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';

const WallPaper = styled('div')({
  position: 'absolute',
  width: '100%',
  height: '100%',
  top: 0,
  left: 0,
  overflow: 'hidden',
  background: 'linear-gradient(rgb(255, 38, 142) 0%, rgb(255, 105, 79) 100%)',
  transition: 'all 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s',
  '&::before': {
    content: '""',
    width: '140%',
    height: '140%',
    position: 'absolute',
    top: '-40%',
    right: '-50%',
    background:
      'radial-gradient(at center center, rgb(62, 79, 249) 0%, rgba(62, 79, 249, 0) 64%)',
  },
  '&::after': {
    content: '""',
    width: '140%',
    height: '140%',
    position: 'absolute',
    bottom: '-50%',
    left: '-30%',
    background:
      'radial-gradient(at center center, rgb(247, 237, 225) 0%, rgba(247, 237, 225, 0) 70%)',
    transform: 'rotate(30deg)',
  },
});

const Widget = styled('div')(({ theme }) => ({
  padding: 16,
  borderRadius: 16,
  width: 343,
  maxWidth: '100%',
  margin: 'auto',
  position: 'relative',
  zIndex: 1,
  backgroundColor: 'rgba(255,255,255,0.4)',
  backdropFilter: 'blur(40px)',
  ...theme.applyStyles('dark', {
    backgroundColor: 'rgba(0,0,0,0.6)',
  }),
}));

const CoverImage = styled('div')({
  width: 100,
  objectFit: 'cover',
  overflow: 'hidden',
  flexShrink: 0,
  borderRadius: 8,
  backgroundColor: 'rgba(0,0,0,0.08)',
  '& > img': {
    width: '100%',
  },
});

const TinyText = styled(Typography)({
  fontSize: '0.75rem',
  opacity: 0.38,
  fontWeight: 500,
  letterSpacing: 0.2,
});

interface Song {
  id: number;
  category: number;
  image: string;
  video: string;
  multi_files: boolean;
  title: string;
  youtube: string;
}

interface Props {
  currentSong: Song;
  isPlaying: Boolean;
  onSongEnd: (id: number) => void;
  onSongNext: (id: number) => void;
  onSongPrev: (id: number) => void;
}

const MusicPlayerSlider: React.FC<Props> = ({ currentSong, isPlaying, onSongEnd, onSongNext, onSongPrev}) => {
  const [duration, setDuration] = useState(0); // seconds
  const [durationTotal, setDurationTotal] = useState(0); // seconds
  const [endAudio, setEndAudio] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [position, setPosition] = useState(0);
  const [sound, setSound] = useState(1);
  const [paused, setPaused] = useState(true);
  const [count, setCount] = useState(0);
  function formatDuration(value) {
    const minute = Math.floor(value / 60);
    const secondLeft = value - minute * 60;
    return `${minute}:${secondLeft < 10 ? `0${secondLeft}` : secondLeft}`;
  }
  /*const melodii = [
   {
    title: 'Adrian-Minune--Asa-Sunt-Zilele-Mele--Muzica-Originala',
    image: imagine,
    song: melodie
  }];*/
  //const [melodii,setMelodii] = useState([]);
  //console.log(melodii);
  const audioRef = useRef(null);
  const videoRef = useRef(null);
  const currentSongRef = useRef(currentSong);

  useEffect(() => {
    currentSongRef.current = currentSong;
  }, [currentSong]); 

  const [category,setCategory] = useState([
      {
        id:1,
        title: 'Muzica Populara'
      },
      {
        id:2,
        title: 'Manele'
      },
      {
        id:3,
        title: 'Usoara'
      }
    ]);

  const [isVideo,setIsVideo] = useState(false);

  useEffect(() => {
    const video = videoRef.current;
    if (isPlaying) {
      video.src = currentSong.video;
      video.play();
    }

  }, [isPlaying, currentSong, onSongEnd]);

  const handleVideoEnd = async () => {
    await onSongEnd();

    setTimeout(()=>{
      audioRef.current.pause();
      setPaused(true);
      setIsVideo(true);
      console.log('handleVideoEnd: '+JSON.stringify(currentSongRef.current));
      if(isVideo){
        videoRef.current.src = currentSongRef.current.url;
      }else{
        videoRef.current.src = null;
      }
    },300);
  }

  const capitalizeFirstLetter = (string) => {
    //string = string.toLowerCase();
    //return string.replace(/^./, string[0].toUpperCase());
    return string
      .toLowerCase()
      .split(' ')
      .map(word => word.charAt(0).toUpperCase() + word.slice(1))
      .join(' ');
  };

  return (
    <Box sx={{ width: '90%', overflow: 'hidden', position: 'relative', p: 3 , margin: '0 auto' }}>
      <Widget>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <CoverImage>
            <img
              alt={currentSong.title}
              src={currentSong.image}
              style={{display:'cover'}}
            />
          </CoverImage>
          <Box sx={{ ml: 1.5, minWidth: 0 }}>
            <Typography
              variant="caption"
              sx={{ color: 'text.secondary', fontWeight: 500 }}
            >
              Duration - {formatDuration(durationTotal)}
            </Typography>
            <Typography noWrap>
              <b>{category[currentSong.category-1].title}</b>
            </Typography>
            <Typography noWrap sx={{ letterSpacing: -0.25 }}>
                {capitalizeFirstLetter(currentSong.title)}
            </Typography>
          </Box>
        </Box>
          <video 
            width={320} 
            ref={videoRef} 
            controls 
            autoPlay={true} 
            style={{margin:'0 auto'}}
            onEnded={handleVideoEnd}
            />
      </Widget>
      <WallPaper />
    </Box>
  );
}

export default MusicPlayerSlider;