import React, { useEffect } from "react";
import { useState } from "react";
import { CircularProgress } from "@mui/material";
import { Suspense } from "react";
import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import ErrorBoundary from "../components/ErrorBoundary";
import Playlist from "../components/playlist/Playlist";
import MusicPlayerSlider from "../components/player/Player";
import imagine_adrian from "../assets/images/Adrian-Minune--Asa-Sunt-Zilele-Mele--Muzica-Originala.png";
import video_adrian from "../assets/videos/Petrica-Mitu-Stoian-colaj-sarbe-live.mp4";

const WallPaper = styled("div")({
  position: "absolute",
  width: "100%",
  height: "100%",
  top: 0,
  left: 0,
  overflow: "hidden",
  background: "linear-gradient(rgb(255, 38, 142) 0%, rgb(255, 105, 79) 100%)",
  transition: "all 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s",
  "&::before": {
    content: '""',
    width: "140%",
    height: "140%",
    position: "absolute",
    top: "-40%",
    right: "-50%",
    background:
      "radial-gradient(at center center, rgb(62, 79, 249) 0%, rgba(62, 79, 249, 0) 64%)",
  },
  "&::after": {
    content: '""',
    width: "140%",
    height: "140%",
    position: "absolute",
    bottom: "-50%",
    left: "-30%",
    background:
      "radial-gradient(at center center, rgb(247, 237, 225) 0%, rgba(247, 237, 225, 0) 70%)",
    transform: "rotate(30deg)",
  },
});

const Widget = styled("div")(({ theme }) => ({
  padding: 16,
  borderRadius: 16,
  width: 343,
  maxWidth: "100%",
  margin: "auto",
  position: "relative",
  zIndex: 1,
  backgroundColor: "rgba(255,255,255,0.4)",
  backdropFilter: "blur(40px)",
  ...theme.applyStyles("dark", {
    backgroundColor: "rgba(0,0,0,0.6)",
  }),
}));

const VideosPage = () => {
  const [totalDuration, setTotalDuration] = useState(0);
  const [currentVideoIndex, setCurrentVideoIndex] = useState(0);
  const [currentPage, setCurrentPage] = useState(0);
  const itemsPerPage = 3;
  const [isPlaying, setIsPlaying] = useState(false);
  const [playlist, setPlaylist] = useState([
    {
      id: 1,
      category: 1,
      image: imagine_adrian,
      video: video_adrian,
      multi_files: false,
      title: "Adrian Minune Asa Sunt Zilele Mele Muzica Originala",
      url: "https://lost-ways.com/slim/public/videos_download/lWZzlHQXhkQ/Adrian-Minune--Asa-Sunt-Zilele-Mele--Muzica-Originala-.mp4",
      youtube: "https://www.youtube.com/watch?v=lWZzlHQXhkQ",
    },
  ]);

  const handleSelectVideo = (index) => {
    console.log(index);
    setCurrentVideoIndex(
      playlist.filter((video) => video.id === index)[0].id - 1
    );
    console.log(currentVideoIndex);
    setIsPlaying(true);
  };

  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);

    if (newPage > Math.ceil(playlist.length / itemsPerPage) - 1) {
      setCurrentPage(Math.ceil(playlist.length / itemsPerPage) - 1);
    } else if (newPage < 0) {
      setCurrentPage(0);
    }
    //setCurrentSongIndex(newPage * itemsPerPage);
  };

  const capitalizeFirstLetter = (string) => {
    var stringCapitalize = "";
    string.split(" ").forEach((element) => {
      stringCapitalize =
        stringCapitalize +
        element.replace(/^./, element[0].toUpperCase()) +
        " ";
    });
    return stringCapitalize;
  };

  const handleVideoEnd = () => {};

  const handleVideoNext = () => {};

  const handleVideoPrev = () => {};

  return (
    <ErrorBoundary>
      <Suspense fallback={<CircularProgress />}>
        <Box
          sx={{
            width: "90%",
            overflow: "hidden",
            position: "relative",
            p: 3,
            margin: "0 auto",
          }}
        >
          <Widget>
            <Box sx={{ display: "flex", flexDirection: "column", alignItems: "start" }}>
              <div style={{margin: '0 auto'}}><strong>Videos Page</strong></div>
              <p>Application Version: {process.env.REACT_APP_VERSION}</p>
              <p>
                Application Name:{" "}
                {capitalizeFirstLetter(
                  process.env.REACT_APP_NAME?.split("-").join(" ")
                )}
              </p>
              <p>React version: {React.version}</p>
            </Box>
          </Widget>

        <Playlist
          songs={playlist}
          onSelectSong={handleSelectVideo}
          currentSongIndex={currentVideoIndex}
          currentPage={currentPage}
          itemsPerPage={itemsPerPage}
          onPageChange={handlePageChange}
          onTotalDuration={totalDuration}
        />
        <MusicPlayerSlider
          currentSong={playlist[currentVideoIndex]}
          isPlaying={isPlaying}
          onSongEnd={handleVideoEnd}
          onSongNext={handleVideoNext}
          onSongPrev={handleVideoPrev}
        />
        <WallPaper />
        </Box>
      </Suspense>
    </ErrorBoundary>
  );
};

export default VideosPage;
