const ChartService = {
  getChartData(): Promise<SalesDataType[]> {
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve([
          { name: "Jan", sales: 4000 },
          { name: "Feb", sales: 3000 },
          { name: "Mar", sales: 5000 },
          { name: "Apr", sales: 7000 },
        ]);
      }, 2000);
    });
  },
};

export default ChartService;
