import { CircularProgress } from "@mui/material";
import { Suspense } from "react";
import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import BarChart from "../components/BarChart";
import ErrorBoundary from "../components/ErrorBoundary";
import ChartService from "../services/api/ChartService";

const WallPaper = styled("div")({
  position: "absolute",
  width: "100%",
  height: "100%",
  top: 0,
  left: 0,
  overflow: "hidden",
  background: "linear-gradient(rgb(255, 38, 142) 0%, rgb(255, 105, 79) 100%)",
  transition: "all 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s",
  "&::before": {
    content: '""',
    width: "140%",
    height: "140%",
    position: "absolute",
    top: "-40%",
    right: "-50%",
    background:
      "radial-gradient(at center center, rgb(62, 79, 249) 0%, rgba(62, 79, 249, 0) 64%)",
  },
  "&::after": {
    content: '""',
    width: "140%",
    height: "140%",
    position: "absolute",
    bottom: "-50%",
    left: "-30%",
    background:
      "radial-gradient(at center center, rgb(247, 237, 225) 0%, rgba(247, 237, 225, 0) 70%)",
    transform: "rotate(30deg)",
  },
});

const Widget = styled("div")(({ theme }) => ({
  padding: 16,
  borderRadius: 16,
  width: 343,
  maxWidth: "100%",
  margin: "auto",
  position: "relative",
  zIndex: 1,
  backgroundColor: "rgba(255,255,255,0.4)",
  backdropFilter: "blur(40px)",
  ...theme.applyStyles("dark", {
    backgroundColor: "rgba(0,0,0,0.6)",
  }),
}));

const DashboardPage = () => {
  const fetchSalesDataPromise = ChartService.getChartData();
  return (
    <ErrorBoundary>
      <Suspense fallback={<div style={{display:'flex', justifyContent: 'center', alignItems: 'center',}}><CircularProgress /></div>}>
        <Box
          sx={{
            width: "90%",
            overflow: "hidden",
            position: "relative",
            p: 3,
            margin: "0 auto",
          }}
        >
          <Widget>
            <Box
              sx={{
                display: "flex",
                flexDirection: "column",
                alignItems: "start",
              }}
            >
              <BarChart fetchSalesData={fetchSalesDataPromise} />
            </Box>
          </Widget>
          <WallPaper />
        </Box>
      </Suspense>
    </ErrorBoundary>
  );
};

export default DashboardPage;
